import Vue from 'vue'
import VueI18n from 'vue-i18n'

Vue.use(VueI18n)

function loadLocaleMessages () {
  const locales = require.context('./locales', true, /[A-Za-z0-9-_,\s]+\.json$/i)
  const messages = {}
  locales.keys().forEach(key => {
    // console.log(`loadLocaleMessages. key: ${key}`)
    const matched = key.match(/([A-Za-z0-9-_]+)\./i)
    if (matched && matched.length > 1) {
      const locale = matched[1]
      messages[locale] = locales(key)
    }
  })
  
  if (window.customLocales) {
    console.log(`loadLocaleMessages. customLocales: ${Object.keys(window.customLocales).length}`);
    for (const locale in window.customLocales) {
      //if (window.customLocales.hasOwnProperty(locale)) {
        console.log(`loadLocaleMessages. custom locale: ${locale}`)
        messages[locale] = window.customLocales[locale]
      //}
    }
  }

  //require('public/locales/en.json')
  // const publiclocals = require.context('../public/locales', true, /[A-Za-z0-9-_,\s]+\.json$/i)
  // console.log(`Load public locale. keys: ${publiclocals.keys().length}`)
  // publiclocals.keys().forEach(key => {
  //   console.log(`Load public locale. key: ${key}`)
  //   const matched = key.match(/([A-Za-z0-9-_]+)\./i)
  //   if (matched && matched.length > 1) {      
  //     const locale = matched[1]
  //     messages[locale] = publiclocals(key)
  //   }
  // });
  return messages
}

export default new VueI18n({
  locale: process.env.VUE_APP_I18N_LOCALE || 'en',
  fallbackLocale: process.env.VUE_APP_I18N_FALLBACK_LOCALE || 'en',
  messages: loadLocaleMessages()
})
